import Vue from 'vue'
import Toasted from 'vue-toasted'

Vue.use(Toasted, {
    iconPack: 'fontawesome',
    duration: 3000
})

Vue.toasted.register(
    'defaultSuccess',
    payload => !payload.msg ? 'Operao realizada com sucesso!' : payload.msg,
    { type: 'success', icon: 'check' }
)

Vue.toasted.register(
    'defaultError',
    payload => !payload.msg ? 'Erro inesperado.' : payload.msg,
    { type : 'error', icon : 'times' }
)

Vue.toasted.register(
    'organizerSuccess',
    payload => !payload.msg ? 'ordenao das tarefas realizada com sucesso!' : payload.msg,
    { type: 'success', icon: 'check' }
)

Vue.toasted.register(
    'taskSuccess',
    payload => !payload.msg ? 'Parabns! Tarefa concluda!' : payload.msg,
    { type: 'success', icon: 'check' }
)

Vue.toasted.register(
    'dailySuccess',
    payload => !payload.msg ? 'Parabns! Dirio concludo!' : payload.msg,
    { type: 'success', icon: 'check' }
)